@tailwind base;
@tailwind components;
@tailwind utilities;


.fade-in {
  opacity: 1;
  transition: opacity 1s ease-in;
  transition-delay: 0.5s; /* Delay the fade-in effect by 0.5 seconds */
}

.fade-out {
  opacity: 0;
  transition: opacity 1s ease-out;
  transition-delay: 0.5s; /* Delay the fade-out effect by 0.5 seconds */
}

.slide-in {
  animation: slide 0.4s linear 1; /* animation-iteration-count를 1로 설정하여 한 번만 실행 */
}

@keyframes slide {
  0% {
    transform: translateX(100%); /* 오른쪽에서 시작 */
  }
  100% {
    transform: translateX(0); /* 왼쪽으로 이동 */
  }
}

/* History.css */

.slide-in-bg {
  animation: slideIn 1s ease-in-out 1;
  animation-fill-mode: forwards;
  transform: translateY(100%);
}

@keyframes slideIn {
  to {
    transform: translateY(0);
  }
}
