@keyframes twinkling {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  10% {
    transform: translateY(300%);
    opacity: 1;
  }
  90% {
    transform: translateY(5000%);
    opacity: 1;
  }
  100% {
    transform: translateY(10000%);
    opacity: 0;
  }
}

.twinkling-stars {
  position: absolute;
  width: 4px;
  height: 4px;
  background: white;
  animation: twinkling 5s linear infinite;
}

@keyframes floatingBubbles {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100vh); /* 이 값은 필요에 따라 조정할 수 있습니다. */
  }
}

.bubble {
  width: 10px; /* 기포의 너비를 조정하세요 */
  height: 10px; /* 기포의 높이를 조정하세요 */
  background-color: skyblue; /* 원하는 색상을 사용하세요 */
  border-radius: 50%; /* 동그라미 모양으로 만듭니다 */
  position: absolute;
  animation: floatingBubbles 10s linear infinite, fadeOut 5s linear infinite; /* 투명도 애니메이션 추가 */
}

@keyframes floatingBubbles {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100vh); /* 이 값은 필요에 따라 조정할 수 있습니다. */
  }
}

@keyframes fadeOut {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.9;
  }
  100% {
    opacity: 1;
  }
}
